.ratesTable {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0 1em;
  justify-content: stretch;
  box-sizing: border-box;
}

.date {
  margin-bottom: 1em;
}

.ratesTable .rate {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: solid 1px #eeeeee88;
  cursor: pointer;
  max-width: calc(50vw - 1em);
}

.rateKey {
  font-size: 0.9em;
  font-family: monospace;
  padding-right: 1em;
  overflow: hidden;
  white-space: nowrap;
}

.rateValue {
  font-size: 1em;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 55%;
}