@keyframes modalKeyframes {
  from {
    transform: translate(-50%, -150%);
    opacity: 0;
  }

  to {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
}

.modal {
  position: fixed;
  z-index: 1;
  left: 50%;
  top: 50%;
  width: 10em;
  height: 10em;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1em;
}

.modal.notification {
  background: #3c424df7;
}

.modal.error {
  background: #7d0d0df7;
  animation: modalKeyframes 0.4s;
}

.modal.closable {
  cursor: pointer;
}

.modal.closable:after {
  content: "close";
  position: absolute;
  bottom: 0.5em;
  text-decoration: underline;
  font-size: 0.7em;
  opacity: 0.8;
}
